import { makeStyles } from '@material-ui/core/styles';
import { pxToRem } from '../../../../styles/themes/vad/constants';

const isOnlyFeaturedTagScenario = (isFeatured, logoImageUrl, isFutureDate) => {
  return (isFeatured && isFeatured.value) && !(logoImageUrl && logoImageUrl.value) && !isFutureDate;
}

const useStyles = makeStyles(theme => ({
  root: {
    marginTop: theme.spacing(-11),
    marginBottom: pxToRem(-30),
    ...theme.mixins.paddingLeft('5.8%'),
    backgroundColor: ({ backgroundColor }) => backgroundColor || theme.mixins.ADCalendar(),
    color: theme.mixins.White(),
    position: 'relative',
    width: 'auto',
    display: 'table',
    ...theme.mixins.paddingRight(theme.spacing(3)),
    zIndex: 2,
    [theme.breakpoints.up('lg')]: {
      marginTop: pxToRem(-42),
      marginBottom: ({ isFeatured, logoImageUrl, isFutureDate }) => isOnlyFeaturedTagScenario(isFeatured, logoImageUrl, isFutureDate) && theme.spacing(6) || pxToRem(-98),
      top: ({ isFeatured, logoImageUrl, isFutureDate }) => isOnlyFeaturedTagScenario(isFeatured, logoImageUrl, isFutureDate) && pxToRem(-34) || pxToRem(-68),
      maxWidth: '50%',
      ...theme.mixins.paddingRight(theme.spacing(7)),
    },

    '&.configurableTimer': {
      marginTop: pxToRem(-42),
      marginBottom: pxToRem(-16),
      color: ({ textColor }) => textColor || theme.palette.primary.main,
      [theme.breakpoints.down('md')]: {
        marginTop: pxToRem(-48.4),
        width: '100%',

        '& .contentWrapper': {
          justifyContent: 'flex-start',
        },
      },
      '& .renderer': {
        [theme.breakpoints.up('lg')]: {
          ...theme.mixins.paddingRight(pxToRem(12)),
        },
        '&:last-child': {
          [theme.breakpoints.up('lg')]: {
            ...theme.mixins.paddingRight(pxToRem(4)),
          },
        },
      },
      '& .MuiTypography-h5': {
        fontFamily: theme.typography.fontFamily.primaryRegular,
        fontSize: pxToRem(16),
        lineHeight: pxToRem(19),
        [theme.breakpoints.up('lg')]: {
          fontSize: pxToRem(36),
          lineHeight: pxToRem(44),
        },
      },
      '& .MuiTypography-body2Bold': {
        [theme.breakpoints.down('md')]: {
          fontFamily: theme.typography.fontFamily.primaryRegular,
          fontSize: pxToRem(12),
          lineHeight: pxToRem(18),
        },
      },
    },

    contentWrapper: {
      width: '100%',
    },

    '& .eventLogo': {
      position: 'relative',
      backgroundColor: theme.mixins.White(),
      ...theme.mixins.marginRight(theme.spacing(2)),
      '& img': {
        position: 'relative',
        top: pxToRem(-32),
        zIndex: 1,
        maxWidth: '120px',
        [theme.breakpoints.up('lg')]: {
          maxWidth: '220px',
          top: pxToRem(-22),
        },
      },
      [theme.breakpoints.up('lg')]: {
        ...theme.mixins.marginRight(theme.spacing(3)),
      },
    },

    '& .timer': {
      paddingTop: pxToRem(16), //# TODO confirm with design
      paddingBottom: pxToRem(16),
      [theme.breakpoints.up('lg')]: {
        paddingTop: pxToRem(36),
        paddingBottom: pxToRem(36),
      },
    },
    background: ({ isFeatured, logoImageUrl, isFutureDate }) => isOnlyFeaturedTagScenario(isFeatured, logoImageUrl, isFutureDate) && 'transparent',
  },
  tag: {
    zIndex: 2,
    position: 'absolute',
    top: pxToRem(-20),
    ...theme.mixins.left(pxToRem(14)),
    borderRadius: pxToRem(3),
    background: 'rgb(239, 81, 87)',
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
    ...theme.mixins.paddingLR(pxToRem(16), pxToRem(16)),
    [theme.breakpoints.up('lg')]: {
      top: pxToRem(-16),
      ...theme.mixins.left(pxToRem(74)),
    },
  },
  renderer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    ...theme.mixins.paddingRight(theme.spacing(1)),
  },
  dateItem: {
    display: 'flex',
    ...theme.mixins.paddingRight(theme.spacing(1)),
  },
}));

export default useStyles;
